body{
  font-family: 'Poppins', sans-serif;
}

.contenedor__ruta{
    display: flex;
    justify-content: center;
    align-items: center;
    
}
.contenedor__error{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    margin:0 auto;
}
.contenedor{
    margin: 0 auto;
    width: 800px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.seccion__button{
    margin-top: 10px;
    outline: none;
    width: 100px;
    background-color: #229954;
    color: #fff;
    height: 40px;
    border-radius: 10px;
    border:none;
    
    
}
button:hover{
    background-color:#214e34;
   cursor: pointer;
}
h3{
    color:#229954;
}
li{
    list-style:none;
}
.contacto{

        width: 250px;
