    grid-column: 1 /span 4;
  
    
}
.home{
    
    width: 100%;
   
    height: 100vh;
   
}

.home__menu{
    margin-bottom: 35px;
    display: flex;
    justify-content: start;
}

.home__menu__item{
    display: flex;
    margin :0px 5px;
    padding:5px;
}

.home__menu__item li{
  
    font-size: 23px;
    margin-left: 10px;
    margin-right: 10px;
    color:#229954;
    font-weight:bold;
    text-decoration:none;
    
}
link{
    text-decoration:none;
}

.home__menu__item li:hover{
  
    
  
    color:#214e34;
    cursor: pointer;
    text-decoration:none;
   
}
span{
    font-weight: bold;
    font-size: 35px;
    color:#214e34 ;
}
.seccion{
    display: flex;
    justify-content: space-around;
    align-items:center;
}
